import { NextResponse } from 'next/server';
import pool from '../../lib/db'; 
import bcrypt from 'bcryptjs'; // ‚úÖ ‡∏≠‡∏¢‡πà‡∏≤‡∏•‡∏∑‡∏° import bcrypt

// --- GET (‡πÄ‡∏´‡∏°‡∏∑‡∏≠‡∏ô‡πÄ‡∏î‡∏¥‡∏°) ---
export async function GET(request) {
  try {
    const { searchParams } = new URL(request.url);
    const userRole = searchParams.get('user_role');
    const userId = searchParams.get('user_id');

    // Default Query
    let sql = `SELECT * FROM employees`;
    const params = [];

    // ‡∏ñ‡πâ‡∏≤‡πÄ‡∏õ‡πá‡∏ô Employee ‡πÉ‡∏´‡πâ‡πÄ‡∏´‡πá‡∏ô‡πÅ‡∏Ñ‡πà‡∏Ç‡∏≠‡∏á‡∏ï‡∏±‡∏ß‡πÄ‡∏≠‡∏á (Optional: ‡∏Å‡∏£‡∏≠‡∏á‡∏ï‡∏≤‡∏° Role)
    if (userRole === 'employee' && userId) {
        // ‡∏Ñ‡πâ‡∏ô‡∏´‡∏≤‡∏à‡∏≤‡∏Å emp_code ‡∏´‡∏£‡∏∑‡∏≠ id (‡πÅ‡∏•‡πâ‡∏ß‡πÅ‡∏ï‡πà‡∏ß‡πà‡∏≤ userId ‡∏™‡πà‡∏á‡∏≠‡∏∞‡πÑ‡∏£‡∏°‡∏≤)
        sql += ` WHERE emp_code = ? OR email = (SELECT email FROM users WHERE id = ?)`;
        params.push(userId, userId);
    } else {
        sql += ` ORDER BY emp_code DESC`;
    }

    const [rows] = await pool.query(sql, params);
    return NextResponse.json(rows);
  } catch (error) {
    return NextResponse.json({ error: error.message }, { status: 500 });
  }
}

// --- POST (‡∏™‡∏£‡πâ‡∏≤‡∏á‡∏û‡∏ô‡∏±‡∏Å‡∏á‡∏≤‡∏ô + ‡∏™‡∏£‡πâ‡∏≤‡∏á User) ---
export async function POST(request) {
  try {
    const body = await request.json();
    
    const { 
      emp_code, first_name, last_name, 
      email, position, 
      phone, birth_date, address, // ‚úÖ 1. ‡∏£‡∏±‡∏ö‡∏Ñ‡πà‡∏≤‡πÉ‡∏´‡∏°‡πà‡πÄ‡∏Ç‡πâ‡∏≤‡∏°‡∏≤
      role_id, role_name, 
      departments_id, departments_name, 
      salary,
      username, 
      password 
    } = body;

    // 2. Validation (‡πÄ‡∏ä‡πá‡∏Ñ‡∏Ñ‡πà‡∏≤‡∏´‡∏•‡∏±‡∏Å‡πÜ ‡∏ó‡∏µ‡πà‡∏´‡πâ‡∏≤‡∏°‡∏ß‡πà‡∏≤‡∏á)
    if (!emp_code || !first_name || !email || !username || !password) {
      return NextResponse.json({ error: '‡∏Å‡∏£‡∏∏‡∏ì‡∏≤‡∏Å‡∏£‡∏≠‡∏Å‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡πÉ‡∏´‡πâ‡∏Ñ‡∏£‡∏ö‡∏ñ‡πâ‡∏ß‡∏ô (‡∏£‡∏´‡∏±‡∏™, ‡∏ä‡∏∑‡πà‡∏≠, ‡∏≠‡∏µ‡πÄ‡∏°‡∏•, User, Pass)' }, { status: 400 });
    }

    // 3. ‡πÄ‡∏ä‡πá‡∏Ñ‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡∏ã‡πâ‡∏≥‡πÉ‡∏ô Users (‡∏Å‡∏±‡∏ô‡πÄ‡∏´‡∏ô‡∏µ‡∏¢‡∏ß)
    const [existingUser] = await pool.query("SELECT id FROM users WHERE username = ? OR email = ?", [username, email]);
    if (existingUser.length > 0) {
        return NextResponse.json({ error: 'Username ‡∏´‡∏£‡∏∑‡∏≠ Email ‡∏ô‡∏µ‡πâ‡∏°‡∏µ‡∏ú‡∏π‡πâ‡πÉ‡∏ä‡πâ‡∏á‡∏≤‡∏ô‡πÅ‡∏•‡πâ‡∏ß' }, { status: 409 });
    }

    // 4. ‡πÄ‡∏£‡∏¥‡πà‡∏°‡∏ö‡∏±‡∏ô‡∏ó‡∏∂‡∏Å: Insert Employee (‡πÄ‡∏û‡∏¥‡πà‡∏° phone, birth_date, address)
    const sqlEmployee = `
      INSERT INTO employees 
      (emp_code, first_name, last_name, email, phone, birth_date, address, position, role_id, role_name, departments_id, departments_name, salary) 
      VALUES (?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?)
    `;
    
    // ‚úÖ ‡∏™‡πà‡∏á‡∏Ñ‡πà‡∏≤‡πÄ‡∏£‡∏µ‡∏¢‡∏á‡∏ï‡∏≤‡∏°‡∏•‡∏≥‡∏î‡∏±‡∏ö VALUES
    await pool.query(sqlEmployee, [
      emp_code, 
      first_name, 
      last_name, 
      email, 
      phone || null,       // ‡∏ñ‡πâ‡∏≤‡πÑ‡∏°‡πà‡∏°‡∏µ‡∏™‡πà‡∏á null
      birth_date || null,  // ‡∏ñ‡πâ‡∏≤‡πÑ‡∏°‡πà‡∏°‡∏µ‡∏™‡πà‡∏á null
      address || null,     // ‡∏ñ‡πâ‡∏≤‡πÑ‡∏°‡πà‡∏°‡∏µ‡∏™‡πà‡∏á null
      position, 
      role_id, 
      role_name, 
      departments_id, 
      departments_name, 
      salary || 0
    ]);

    // 5. ‡∏™‡∏£‡πâ‡∏≤‡∏á User ‡πÉ‡∏´‡∏°‡πà‡πÉ‡∏´‡πâ‡∏ó‡∏±‡∏ô‡∏ó‡∏µ
    const hashedPassword = await bcrypt.hash(password, 10); 

    const sqlUser = `
        INSERT INTO users 
        (username, email, password, role, role_id, department_id, employee_id) 
        VALUES (?, ?, ?, ?, ?, ?, ?)
    `;

    // ‡πÅ‡∏õ‡∏•‡∏á Role Name ‡πÉ‡∏´‡πâ‡πÄ‡∏õ‡πá‡∏ô format ‡∏°‡∏≤‡∏ï‡∏£‡∏ê‡∏≤‡∏ô (‡πÄ‡∏ä‡πà‡∏ô Super Admin -> super_admin)
    const roleString = role_name ? role_name.toLowerCase().replace(/\s+/g, '_') : 'employee'; 

    await pool.query(sqlUser, [
        username,
        email,
        hashedPassword,
        roleString,       
        role_id,          
        departments_id,   
        emp_code // ‚úÖ ‡πÄ‡∏ä‡∏∑‡πà‡∏≠‡∏°‡∏î‡πâ‡∏ß‡∏¢ emp_code ‡∏ï‡∏≤‡∏°‡πÇ‡∏Ñ‡∏£‡∏á‡∏™‡∏£‡πâ‡∏≤‡∏á DB ‡∏Ç‡∏≠‡∏á‡∏Ñ‡∏∏‡∏ì
    ]);
    
    return NextResponse.json({ 
        message: '‡πÄ‡∏û‡∏¥‡πà‡∏°‡∏û‡∏ô‡∏±‡∏Å‡∏á‡∏≤‡∏ô‡πÅ‡∏•‡∏∞‡∏™‡∏£‡πâ‡∏≤‡∏á‡∏ö‡∏±‡∏ç‡∏ä‡∏µ‡∏ú‡∏π‡πâ‡πÉ‡∏ä‡πâ‡πÄ‡∏£‡∏µ‡∏¢‡∏ö‡∏£‡πâ‡∏≠‡∏¢‡πÅ‡∏•‡πâ‡∏ß!', 
        emp_code 
    }, { status: 201 });

  } catch (error) {
    console.error("Database Error:", error);
    // ‡∏î‡∏±‡∏Å Error ‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡∏ã‡πâ‡∏≥ (Duplicate Entry)
    if (error.code === 'ER_DUP_ENTRY') {
      return NextResponse.json({ error: `‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡∏ã‡πâ‡∏≥ (‡∏£‡∏´‡∏±‡∏™‡∏û‡∏ô‡∏±‡∏Å‡∏á‡∏≤‡∏ô ‡∏´‡∏£‡∏∑‡∏≠ ‡∏≠‡∏µ‡πÄ‡∏°‡∏• ‡∏≠‡∏≤‡∏à‡∏°‡∏µ‡∏≠‡∏¢‡∏π‡πà‡πÉ‡∏ô‡∏£‡∏∞‡∏ö‡∏ö‡πÅ‡∏•‡πâ‡∏ß)` }, { status: 409 });
    }
    return NextResponse.json({ error: error.message }, { status: 500 });
  }
}